import {FAQSData, FAQSLimitedData} from "/data/faqs.js";

const { useState, Fragment } = React;

const FAQS = ({fullData, limitedData}) => {

  const limited = document
    .querySelector("#FAQS")
    .getAttribute("limited");

  const [open, setOpen] = useState({});

  const toggleFAQ = id => {
    setOpen(prevopen => ({
      ...prevopen,
      [id]: !prevopen[id]
    }));
  };

  const data = limited ? limitedData : fullData;

  const FAQ = ({id, question, answer}) => (
    <div className="faq-bar" onClick={() => toggleFAQ(id)}>
      <div className="faq-bar faq-bar__heading">
        <strong>{question}</strong>
          <figure>
            <img src={require('/assets/icons/plus.svg')} alt="plus"/>
          </figure>
      </div>
      {open[id] ? <p>{answer}</p> : null}
    </div>
  )

  return (
    <Fragment>
      {data.map(item => (
        <FAQ key={item.id} id={item.id} question={item.question} answer={item.answer} /> 
      ))}
    </Fragment>
  );
};

const domContainer = document.querySelector('#FAQS');
ReactDOM.render(<FAQS fullData={FAQSData} limitedData={FAQSLimitedData} />, domContainer);	